<template>
  <div class="content">
    <div class="content-tit">
      <button type="button" class="btn back" @click="goBack"></button>
      구독 해지
    </div>
    <ul class="text-list m-t--20">
      <li class="text-list--item">
        <span class="left">이용기간</span>
        <span class="right">2022.05.30 까지</span>
      </li>
      <li class="text-list--item">
        <span class="left">다음 결제일</span>
        <span class="right">2022.05.31</span>
      </li>
      <li class="text-list--item">
        <span class="left">결제상품</span>
        <span class="right">월간 결제</span>
      </li>
      <li class="text-list--item">
        <span class="left">결제금액</span>
        <span class="right">30,000원</span>
      </li>
      <li class="text-list--item bottom-line">
        <span class="left">결제수단</span>
        <span class="right">신용카드 (BC 1111 03)</span>
      </li>
    </ul>
    <div class="btn-wrap m-t--40">
      <button type="button" class="btn btn-full btn-black">
        구독 해지 예약
      </button>
    </div>
    <div class="text-center font-color--grey font-size--14 text-height--15">
      지금 해지 예약하셔도<br />2022.05.30까지 이용하실 수 있습니다.
    </div>
  </div>
</template>

<script>
export default {
  name: "MySubCancel",
  components: {},
  data() {
    return {
      userData: {
        mid: "INIBillTst", //*   streamin01
        authtype: "D",
        orderid: "", //*
        price: "1000",
        goodname: "test01",
        buyername: "testName",
        buyeremail: "testName@test.com",
        buyertel: "010-0000-0000",
        returnurl: "https://youarethe.co.kr/api/pay/inicis",
        closeurl: "https://youarethe.co.kr/mypage/sub",
        timestamp: "", //*
        period: "M2", //["Y2":년단위결제, "M2":월단위결제, "YYYYMMDDYYYYMMDD":시작일종료일]
        hashdata: "",
      },
      liteKey: "b09LVzhuTGZVaEY1WmJoQnZzdXpRdz09", //"4Kxq4jZEtd5tsaaf"
      prmotions: [],
      products: {
        Description: "",
        DesList: [],
        Name: "",
        No: "",
        MonthlyPrice: 0,
        Price: 0,
        YearlyDiscountRate: 0,
      },
      finalInfo: {
        Price: 0,
        Prmotions: { DiscountRate: 0 },
      },
      isPrmotion: null,
      promotion: false,
      promotionRegister: false,
    };
  },
  filters: {
    comma(val) {
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
  created() {
    this.getPro();
    this.checkMsg();
  },
  methods: {
    checkMsg() {
      // this.$route.query.id
      if ("message" in this.$route.query && this.$route.query.message != "") {
        this.$store.commit("updateError", this.$route.query.message);
        delete this.$route.query.message;
        history.replaceState({}, null, location.pathname);
      }
    },
    selectType(st) {
      this.finalInfo.Prmotions = { DiscountRate: 0 };
      this.isPrmotion = null;
      if (st == "M2") {
        this.finalInfo.Price = this.products.MonthlyPrice;
      } else if (st == "Y2") {
        this.finalInfo.Price =
          this.products.MonthlyPrice * 12 -
          this.products.MonthlyPrice *
            12 *
            (this.products.YearlyDiscountRate * 0.01);
      }
    },
    checkPrmo() {
      if (this.isPrmotion != null) {
        if (this.userData.period == "M2") {
          if (this.isPrmotion.IsMonthly) {
            this.finalInfo.Prmotions = this.isPrmotion;
          } else {
            this.$store.commit(
              "updateError",
              "해당 프로모션은 월간 결제유형이 아닙니다."
            );
          }
        } else if (this.userData.period == "Y2") {
          if (this.isPrmotion.IsYearly) {
            this.finalInfo.Prmotions = this.isPrmotion;
          } else {
            this.$store.commit(
              "updateError",
              "해당 프로모션은 연간 결제유형이 아닙니다."
            );
          }
        }
      } else {
        this.finalInfo.Prmotions = { DiscountRate: 0 };
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    getPro() {
      this.$apiGET("/api/pay/products").then(({ data }) => {
        this.products = data;
        this.products.DesList = this.products.Description.split("\n");

        this.$apiGET("/api/pay/promotions").then(({ data }) => {
          this.prmotions = data;

          this.selectType("M2");
        });
      });
    },
    checkForm() {
      var sha256 = require("js-sha256");

      this.userData.timestamp = new Date().getTime();

      if ("Id" in this.finalInfo.Prmotions) {
        this.userData.orderid =
          this.userData.period[0] +
          this.products.Id +
          "-" +
          this.$store.state.user.Id +
          "-" +
          this.finalInfo.Prmotions.Id +
          "-" +
          this.userData.timestamp;
      } else {
        this.userData.orderid =
          this.userData.period[0] +
          this.products.Id +
          "-" +
          this.$store.state.user.Id +
          "-" +
          this.userData.timestamp;
      }

      this.userData.hashdata = sha256(
        this.userData.mid +
          this.userData.orderid +
          this.userData.timestamp +
          this.liteKey
      );

      this.userData.price =
        this.finalInfo.Price -
        this.finalInfo.Price * this.finalInfo.Prmotions.DiscountRate * 0.01;

      this.userData.goodname = this.products.Name;

      return true;
      // e.preventDefault();
    },
  },
};
</script>
